.body-product {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: rgba(1, 197, 242, 0.3);
  border-radius: 10px 10px;
  margin-top: 10px;
}
.botton-carts {
  padding-right: 40px;
}
.img-product {
  img {
    max-height: 70px;
    border-radius: 10px 0px 0px 10px;
    margin-bottom: -4px;
  }
}
.input-cont {
}
.price {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 20px;
  color: aliceblue;
}
.price-prod {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 40%;
}
.title-ptoduct {
}

.name-prod {
  font-size: 1em;
}
.text-prod {
  font-size: 0.5em;
}

@media (max-width: 960px) {
  .body-product {
    flex-flow: column nowrap;
    justify-content: center;
    align-items: center;
  }
  .botton-carts {
    padding-right: 1px;
  }
  .img-product {
    img {
      max-height: 250px;
      width: 100%;
      border-radius: 10px 10px 0px 0px;
      margin-bottom: -4px;
    }
  }
  .name-prod {
    font-size: 2em;
  }
  .text-prod {
    font-size: 1em;
  }
  .price-prod {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
  }
  .input-cont {
    margin: 0 10px;
  }
}

@media (max-width: 410px) {
  .price-prod {
    flex-flow: column nowrap;
    justify-content: center;
    align-items: center;
  }
  .botton-carts {
    margin-top: 10px;
    margin-bottom: 10px;
  }
}
